import React from 'react'
import ReactApexChart from 'react-apexcharts'
import SpinnerComponent from '../../utils/spinner'
import { currency_type_display, truncate } from "../../utils/_funcs"

const mapYears = (data, depots, currency_type, month, dashboard_metric) => {
    let months = data

    let depots_value = []
    for (let i = 0; i < depots.length; i++) {
        let depot = months.filter(obj => {
            return obj.depot__name === depots[i].name
        })
        let depot_sum = 0
        if (month === 13) {
            depot_sum = depot.reduce((accumulator, object) => {
                return accumulator + object.sum;
            }, 0);
        } else {
            depot_sum = depot.filter(obj => {
                return obj.date__month === month + 1
            })

            depot_sum = depot_sum.length > 0 ? (dashboard_metric ? depot_sum[0].sum : depot_sum[0].quantity) : 0
        }

        depots_value.push(currency_type === "Million" ? truncate(depot_sum / 1000000) : truncate(depot_sum / 1000))
    }
    return depots_value
}

const update = (props) => {
    const { depot_product_month, depot_product_month_month, depot_product_month_year, depot_list, currency_type } = props
    let data = null;
    let series = []
    let year = parseInt(depot_product_month_year)
    let month = parseInt(depot_product_month_month)
    for (let i = 0; i < depot_product_month.length; i++) {
        data = depot_product_month[i].revenue
        const result = mapYears(data, depot_list, currency_type, month, props.dashboard_metric)
        series.push({ "name": depot_product_month[i].name, "data": result })
    }


    return series
}

class DepotProductMonth extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            spinner: false,
            series: [],
            options: {
                chart: {
                    type: 'bar',
                    height: 430
                },
                plotOptions: {
                    bar: {
                        horizontal: false,
                        dataLabels: {
                            position: 'top',
                        },
                    }
                },
                dataLabels: {
                    enabled: true,
                    offsetX: -6,
                    style: {
                        fontSize: '12px',
                        colors: ['#fff']
                    }
                },
                stroke: {
                    show: true,
                    width: 1,
                    colors: ['#fff']
                },
                title: {
                    text: this.props.dashboard_metric ? 'Depots Comparison by Revenue' : 'Depots Comparison by Quantity'
                },
                yaxis: {
                    title: {
                        text: this.props.dashboard_metric ? "Kshs (Millions)" : "Litres"
                    },
                    min: 0
                },

                tooltip: {
                    shared: true,
                    intersect: false,
                    y: {
                        formatter: this.props.dashboard_metric ? function (val) {
                            return `Kshs ${val} Millions`
                        } : function (val) {
                            return `${val} Litres`
                        }
                    }
                },
                xaxis: {
                    categories: [],
                },
            },


        };
    }



    mapMonths = (data, year, month) => {
        let result = data.filter(obj => {
            return parseInt(obj.year) === parseInt(year)
        })

        let values = result[0].values.filter(obj => {
            return parseInt(obj.month) === parseInt(month) + 1
        })
        let series = []
        for (let i = 0; i < values.length; i++) {
            series.push({ "name": values[i].name, "data": values[i].values })
        }
        return series
    }


    static getDerivedStateFromProps = (props, state) => {

        let depots = []

        if (props.update_depot_product_month) {
            state.spinner = true
            return state
        } else {
            state.spinner = false
        }
        for (let i = 0; i < props.depot_list.length; i++) {
            depots.push(props.depot_list[i].name)
        }

        state.series = update(props)
        state.options.xaxis.categories = depots
        state.options.yaxis.title.text = props.dashboard_metric ? `Kshs (${props.currency_type})` : `Litres (${props.currency_type})`
        state.options.tooltip.y.formatter = props.dashboard_metric ? function (val) {
            return `Kshs ${val}${currency_type_display(props.currency_type)}`
        } : function (val) {
            return `${val}${currency_type_display(props.currency_type)} Litres`
        }

        return state
    }

    render() {

        return (
            <div id="chart">
                {this.state.spinner ? <div style={{ height: 380, position: "relative" }}><SpinnerComponent spinner={this.state.spinner} /></div> :
                    <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height={380} />
                }
            </div>
        );
    }
}


export default DepotProductMonth